<template>
  <div class="comment-reply">
    <van-nav-bar
      :title="
        comment.reply_count > 0 ? `${comment.reply_count}条回复` : '暂无回复'
      "
    >
      <van-icon slot="left" name="cross" @click="$emit('close')" />
    </van-nav-bar>
    <div class="scroll-wrap">
      <!-- 当前评论项 -->
      <comment-item :comment="comment" />

      <!-- 评论的回复列表 -->
      <van-cell title="全部回复"></van-cell>
      <comment-list
        :source="comment.com_id"
        type="c"
        :list="commentList"
      ></comment-list>
    </div>

    <!-- 底部区域 -->
    <div class="post-wrap">
      <van-button class="post-btn" size="mini" round @click="isPostShow = true"
        >写评论</van-button
      >
    </div>

    <!-- 发布评论 -->
    <van-popup v-model="isPostShow" position="bottom">
      <!-- 给 CommentPost 组件 v-if="isPostShow" 保证弹出输入框的聚焦 -->
      <CommentPost
        v-if="isPostShow"
        :target="comment.com_id"
        @post-success="onPostSuccess"
      ></CommentPost>
    </van-popup>
  </div>
</template>

<script>
import commentItem from './comment-item.vue'
import commentList from './comment-list.vue'
import CommentPost from './comment-post.vue'
export default {
  components: {
    commentItem,
    commentList,
    CommentPost
  },
  name: 'CommentReply',
  props: {
    comment: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isPostShow: false,
      commentList: [] // 评论的回复列表
    }
  },
  created() {},
  methods: {
    onPostSuccess(data) {
      // 更新回复的数量
      this.comment.reply_count++
      // 关闭弹出层
      this.isPostShow = false
      // 将最新回复的内容展示到列表的顶部
      this.commentList.unshift(data.new_obj)
    }
  }
}
</script>

<style lang="less" scoped>
.scroll-wrap {
  position: fixed;
  top: 92px;
  left: 0;
  right: 0;
  bottom: 88px;
  overflow-y: auto;
}
.post-wrap {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 88px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-top: 1px solid #d8d8d8;
  .post-btn {
    width: 60%;
  }
}
</style>
